'use client';

import dynamic from 'next/dynamic';
import { 
  Box, 
  Container, 
  Typography, 
  useTheme,
  AppBar,
  Toolbar,
  Button,
  IconButton,
  useMediaQuery
} from '@mui/material';
import { ArrowBack as ArrowBackIcon } from '@mui/icons-material';
import { useRouter } from 'next/navigation';

// Dynamically import the LoginForm component with no SSR
const LoginForm = dynamic(() => import('@/components/Login'), {
  ssr: false,
  loading: () => (
    <Box display="flex" justifyContent="center" alignItems="center" minHeight="60vh">
      <Typography>Loading admin login form...</Typography>
    </Box>
  ),
});

export default function AdminLoginPage() {
  const theme = useTheme();
  const router = useRouter();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100vh',
        background: theme.palette.mode === 'light' 
          ? 'linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%)' 
          : theme.palette.background.default,
      }}
    >
      <AppBar 
        position="sticky"
        elevation={0}
        sx={{
          backgroundColor: 'background.paper',
          color: 'text.primary',
          borderBottom: `1px solid ${theme.palette.divider}`,
          backdropFilter: 'blur(10px)',
          background: 'rgba(255, 255, 255, 0.8)',
        }}
      >
        <Container maxWidth="lg" disableGutters>
          <Toolbar disableGutters sx={{ px: { xs: 2, sm: 3 } }}>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="back"
              onClick={() => router.push('/')}
              sx={{ mr: 2, color: 'primary.main' }}
            >
              <ArrowBackIcon />
            </IconButton>
            
            <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
              <Typography 
                variant="h6" 
                component="h1" 
                sx={{
                  fontWeight: 700,
                  background: 'linear-gradient(45deg, #3f51b5 30%, #2196f3 90%)',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',
                  display: { xs: 'none', sm: 'block' },
                }}
              >
                DSA Practice Platform
              </Typography>
              <Typography 
                variant="h6" 
                component="span" 
                sx={{
                  ml: 1,
                  fontWeight: 500,
                  color: 'text.secondary',
                  display: { xs: 'none', md: 'block' },
                }}
              >
                / Admin Portal
              </Typography>
            </Box>

            <Button 
              color="primary" 
              variant="outlined" 
              size={isMobile ? 'small' : 'medium'}
              onClick={() => router.push('/login')}
              sx={{
                textTransform: 'none',
                borderRadius: 2,
                fontWeight: 500,
                px: 3,
              }}
            >
              User Login
            </Button>
          </Toolbar>
        </Container>
      </AppBar>

      <Container
        component="main"
        maxWidth="md"
        sx={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          py: 6,
          px: { xs: 2, sm: 4 },
        }}
      >
        <Box
          sx={{
            maxWidth: 500,
            width: '100%',
            mx: 'auto',
            my: 'auto',
            backgroundColor: 'background.paper',
            borderRadius: 2,
            boxShadow: theme.shadows[3],
            p: { xs: 3, sm: 4 },
            border: `1px solid ${theme.palette.divider}`,
          }}
        >
          <Typography 
            variant="h5" 
            component="h2" 
            gutterBottom 
            sx={{
              fontWeight: 700,
              mb: 3,
              display: 'flex',
              alignItems: 'center',
              gap: 1,
              color: 'primary.main',
            }}
          >
            Admin Sign In
          </Typography>
          
          <LoginForm isAdmin={true} />
        </Box>
      </Container>

      <Box
        component="footer"
        sx={{
          py: 3,
          px: 2,
          mt: 'auto',
          backgroundColor: 'background.paper',
          textAlign: 'center',
          borderTop: `1px solid ${theme.palette.divider}`,
          color: 'text.secondary',
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="body2">
            Â© {new Date().getFullYear()} DSA Practice Platform. All rights reserved.
          </Typography>
        </Container>
      </Box>
    </Box>
  );
}
